import { AfterViewInit, Component, Input, OnInit } from '@angular/core';
import { MaDataGridCell } from '../../interfaces/ma-data-grid-cell';
import { MaDataGridColumnOptions } from '../../interfaces/ma-data-grid-options';

@Component({
  selector: 'ma-data-grid-cell-boolean',
  template: '<div style="text-align: center"><i class="tiny material-icons">{{icon}}</i></div>'
  
})
export class DataGridCellBooleanComponent implements MaDataGridCell, OnInit {

  @Input() data: any;
  @Input() col: MaDataGridColumnOptions;

  icon: string = '';

  constructor() { }

  ngOnInit(): void {
    // console.log(this.data[this.col.prop]);
    if (this.data[this.col.prop] === true) {
      this.icon = 'check_box'
    } else if (this.data[this.col.prop] === false) {
      this.icon = 'check_box_outline_blank'
    }
  }

}
