import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import * as M from 'materialize-css';

@Component({
  selector: 'ma-data-grid-datepicker',
  templateUrl: './data-grid-picker-date.component.html',
  styleUrls: ['./data-grid-picker-date.component.css']
})
export class DataGridPickerDateComponent implements OnInit, OnDestroy, AfterViewInit {

  instance: any;
  datevalue: Date = null;
  realValue: string = "";
  time: string = '';
  @Input() value : string = '';
  @Input() type: 'date' | 'time';
  @Output() changePicker = new EventEmitter<any>();

  datepicker_id: string = "dp_" + Math.floor((Math.random() * 100000));
  @ViewChild("madatepicker", { static: false }) madatepicker: ElementRef;

  constructor() { }
  ngAfterViewInit(): void {
    this._init();
  }
  ngOnDestroy(): void {
    if (this.instance)
      this.instance.destroy();
  }

  ngOnInit(): void {
    // console.log('ngOnInit this.madatepicker');
    if (this.value == '') {
      this.datevalue = null
    }
  }

  getDate() {
    return this.datevalue;
  }

  setDate(date) {
    
    if (date != null) {
      const offset = new Date().getTimezoneOffset()
      date = new Date(date.getTime() - (offset*60*1000))
    }
    var elem = <HTMLInputElement> document.getElementById(this.datepicker_id);
    if (date == null) {
      elem.value = '';
    } else {
      elem.value = date.toISOString().replace(/T.+/,'')
    }
    this.datevalue = date;
    // console.log("setDate VALUE", this.datevalue);
  }

  getTime() {
    return this.time
  }

  setTime(hour, min) {
    if (hour < 10) {
      hour = '0' + hour
    }
    if (min < 10) {
      min = '0' + min
    }
    this.time = hour + ':' + min;
    // console.log("VALUE", this.time);
  }

  _init(): void {
    //var elems = document.querySelectorAll('.ma-data-grid-datepicker');
    var elem = <HTMLInputElement> document.getElementById(this.datepicker_id);
    var ptr = this;
    
    if (this.type == 'date') {
      var instances = M.Datepicker.init(elem, {
        autoClose: true,
        format: 'yyyy-mm-dd',
        onSelect: function (d) {
          ptr.setDate(d);
        }
        //minDate: new Date(),
        //maxDate: new Date(),
      });
      elem.onchange = function (e) {
        // console.log("EVENT", elem.value);
        if (elem.value == '' || !elem.value.match(/^\d\d\d\d-\d\d-\d\d$/)) {
          elem.value = ''
          ptr.setDate(null);
        }
        ptr.emitDateEvent();

      };
      
    } else {
      var instances = M.Timepicker.init(elem, {
        autoClose: true,
        //format: 'yyyy-mm-dd',
        twelveHour: false,
        onSelect: function (d, h, m) {
          //console.log("G",d,h,m)
          ptr.setTime(d, h);
          ptr.emitTimeEvent();
        }
      });
    }

    //this.instance= instances[0];
    //this.instance.setDate(new Date());
    //instance.gotoDate(new Date());
  }

  onChange() {
    // console.log("realValue" + this.realValue);
    if (this.type == 'date') {
      this.changePicker.emit(this.datevalue);
    } else {
      this.changePicker.emit(this.time);
    }
  }

  emitDateEvent() {
    this.changePicker.emit(this.datevalue);
  }

  emitTimeEvent() {
    this.changePicker.emit(this.time);
  }

}
